.container{
    background-color: white;
    
}
.header{
    margin-top: 15px;
    text-align: center;
}
.slider{
    width: 100%;
    height: 40vh;
    margin-bottom: 2%;
}
.slider img{
    object-fit: cover;
    height: 40vh;
}


.div2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2% 5%;
    padding: 20px; 
}

.div2 img {
    margin-left: 25%;
    width: 50%; /* Ensure the image fills the container */
    height: auto;
    object-fit: cover;
    border-radius: 7px;
    padding-top: 20px;
}
.div2 p{
    text-align: center;
}
.service1 {
    width: 100px; /* Set the desired width */
    height: 100px; /* Set the desired height */
    background-color: rgb(235, 235, 227);
    margin: 10px; /* Add margin between service boxes */
    transition: transform 0.3s ease, box-shadow 0.3s ease; 
    border-radius: 20px;
}

.service1:hover {
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    cursor: pointer;
    background-color: rgb(239, 244, 240);
    transform: scale(1.1); 
}


.div3{
    height: 80vh;
    background-color: skyblue;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    margin: 10px;
    color: red;

    
}

.row1-item1{
    color: red;
    margin: 15px;
    display: flex;
    height: 30px;
    width: 300px;
}
.row1-item1.row1{
    display: flex;
    
}
.row2{
    display: flex;
}
.item1-img{
    width: 25vh;
    height: 16vh;
}



.div4{
    height: 30vh;
    background-color: gray;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}



.last-card{
    display: flex;
}


