.main-card-container {
    display: flex;
    justify-content: space-around;
    margin: 20px 20px ;
  }
  
  .main-card {
    /* background-color: #f4f4f4; */
    padding: 20px;
    width: 45%;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin-top: 20px;
    background-color: white;
  }
  .main-card h3{
    padding: 5px;
  
  }
  .inner-card-container {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    background-color: white;
  }
  
  .inner-card {
    background: transparent;
    width: 30%;
    border-radius: 4px;

    text-align: center;
    border-radius: 10px;
  }

  .inner-card img:hover {
    transform: scale(1.05);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    cursor: pointer;
  }

  .inner-card img{
    object-fit: cover;
    width: 100%;
    border-radius: 10px;
  }
  .inner-card p{
    margin: 3px;
  }
